*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

.main-container{
  height: 350px;
  width:300px;
  padding:20px;
  border:2px solid silver;
  margin:100px 500px;
  background:linear-gradient(rgba(0,0,0,1),rgba(0,0,0,1));
  border:none;
}

.input-field {
  width:100%;
  height:50px;
  margin-bottom: 10px;
  background-image: linear-gradient(315deg, #ff7878 0%, #ff0000 74%);
  border:none;
  outline:none;
  font-size:2rem;
  color:whitesmoke;
  font-weight:bold;
}

.buttons-container {
  display:grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap:5px;
}

.btn {
  padding:10px;
  background-image: linear-gradient(315deg, #f977ce 0%, #c373f2 74%);
  color:whitesmoke;
  font-weight:bolder;
  font-size:1.2rem;
  border:none;
  border-radius: 10px;
  text-align: center;
  margin-bottom:5px;
}

.btn:hover {
  opacity:0.9;
}